.container-tb {
    max-width: 900px !important;
    margin: 0 auto;
    padding: 0 20px;
}

h1.your-cart{
    font-size: 36px;
    font-weight: 600;
    margin-top:30px ;
    margin-bottom: 30px;
}

.table-cart {
    text-align: center;
}

.table {
    width: 100% !important;
    border-collapse: collapse !important;
    border: 0 !important;
}

.table.table-cart thead {
    background-color: #970e3d !important;
   
}

.table thead{
    color: #fff !important;
    
    border-top: 0 !important;
}

.th-product {
    text-align: left;
    padding-left: 25px;
}
.table th {
    padding: 15px !important;
}

tbody, td, tfoot, th, thead, tr {
    border-style: none !important;
}

.table tbody {
    font-size: 18px;
    border: 1px solid #fff;
}
.table tbody tr:nth-child(odd) {
    background-color: #f2ecee;
    background-color: var(--neutral-5);
}

.table-cart tbody td:first-child {
    display: flex;
    align-items: center;
    grid-gap: 15px;
    gap: 15px;
    padding-left: 20px;
}

.table td {
    padding: 10px !important;
}

.table tbody tr:nth-child(2n) {
    background-color: #fff;
}

tfoot {
    display: table-footer-group;
    vertical-align: middle;
    border-color: inherit;
}

tr {
    display: table-row;
    vertical-align: inherit;
    border-color: inherit;
}

.cart-highlight {
    background-color: #970e3d !important;
    
    color: #fff;
}

.cheese-hidden {
    display: none;
}

.tomato-hidden {
    display: none;
}

.pinapple-hidden{
    display: none;
}

.milk-hidden{
    display: none;
}

.empt-cart{
    visibility: hidden;
    font-weight: bold;

}

.empt-table{
    visibility: hidden;
}

.btn-pay{
    color: #fff !important;
    background-color: #970e3d !important;
    border-style: none;
    padding: 10px;
    border-radius: 20px;
}

.btn-pay:hover {
    color: #970e3d !important;
    background-color: #fff !important;
    border: 2px solid #970e3d !important;
    font-size: 18px;
}

.paymnet-btn{
    position: fixed !important;
    right: 20%;
    align-items: right !important;
}

.pay-form {
    padding-top: 30px;
}


.label {
    display: block !important;
    font-weight: 700 !important;
}

.input {
    font-size: 16px;
    margin-top: 1em;
    margin-bottom: 1.5em;
    padding: .75em .5em;
    min-width: 200px;
    border: none;
    border-left: 7px solid #d9cfd3 !important;
    transition: border-left-color .16s;
    background-color: #fff;
}
.input:focus {
    outline: none;
}
.input:focus{
    border-left: 7px solid#2b8379 !important;
}

.check_btn:hover{
    background-color: #19a596 !important;
}