.navbar {
    background-color: #fff;
    padding: 15px 10px 10px !important;
    box-shadow: 0 3px 7px 1px rgb(0 0 0 / 7%), 0 -3px 7px 1px rgb(0 0 0 / 7%);
    display: flex;
    align-items: center;
    justify-content: space-between;
    
}

.nav-brand, .nav-brand:hover {
    text-decoration: none;
}


.nav-brand {
    font-size: 30px !important;
    font-weight: 450;
    padding-left: 20px;
    color: #000;
    color: initial;
}

a.product-active{
    font-weight: 800 !important; 
}

.nav-brand:hover{
    color: black;
}

.navbar ul {
    list-style-type: none;
    overflow: hidden;
    padding-right: 20px;
    text-align: center;
}


.nav-item a, .navbar li {
    display: inline-block;
}


.nav-item a{
    text-decoration:  none;
    font-weight: 400;
    color: #2b8379 !important;
    text-align: center;
    padding: 13px 20px !important;
    min-width: 75px;
}

.nav-cart{
    width: 115px !important;
    text-align: center ;
    padding: 10px 25px ;
    
}

.btn-accent{
    color: #fff !important;
    background-color: #970e3d !important;
    font-weight: 900;
}


  
.btn-accent:hover{
    background-color: #ce3167 !important;
}


.btn {
    border-radius: 3px ;
    display: inline-block ;
    cursor: pointer;
    font-size: 14px;
    font-weight: 600 !important ;
    padding: 13px 20px;
    border: 0;
    transition: background-color .2s,color .2s;
}



.container{
    max-width: 900px !important;
    display: flex !important;
    justify-content: center !important;
}

.products-layout {
    padding-top: 40px !important;
    padding-bottom: 40px !important;
    text-align: center !important;
}

h1 {
    font-size: 36px;
    font-weight: 600;
}
p {
    line-height: 30px;
    font-size: 18px;
}
.products-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 25px;
    
}

.product {
    background-color: #fff;
    padding: 15px 20px;
    border-radius: 25px;
    display: grid;
    grid-template-columns: auto 1fr auto; }

.product-image-container {
        position: relative;
    }

    a {
        color: #19a596;
        color: var(--primary-2);
        font-weight: 700;
        text-decoration: none;
    }

    .product-image {
        background-color: #f2ecee !important;
        padding: 10px;
        border-radius: 20px;
        transition: transform .18s ease-out;
    }

    .product-quantity-container {
        position: absolute;
        top: 8px;
        right: -15px;
        background-color: #fff;
        border-radius: 50%;
        padding: 5px;
    }
    .hide-product-quantity-container {
        visibility: hidden;
    }

.hide-remover{
    visibility: hidden;
}
    .product-quantity {
         background-color: #2b8379 !important; 
         
         color: #fff; 
         border-radius: 50%; 
         font-size: 14px; 
         width: 23px; 
         height: 23px; 
         display: grid; 
         place-items: center; 
    }

    .product-info {
        padding-left: 25px;
    }

    .product h3, .product p {
        margin: 0;
    }

    h3 {
        font-size: 24px;
        font-weight: 400;
    }

    p {
        line-height: 30px;
        font-size: 18px;
    }

    .product-checkout {
        display: grid !important;
        grid-template-rows: 1fr auto !important;
        justify-items: end !important; 
    }

    .btn-outline {
        color: #2b8379 !important;
        background-color: #7fd1c7 !important;
        border: 2px solid #2b8379 !important;
        box-shadow: 1px 1px 7px #c4c4c4 !important;
        padding: 8px 25px !important;
        border-radius: 10px !important;
    }

    .btn-outline:hover {
       color: #fff !important;
       background-color: #3c978d !important;           
    }

    .product-delete:hover{
        color: white !important;
        background-color: #c43464 !important;
    }

    .product-delete {
        background-color: #fff !important;
        padding: 5px 10px !important;
        color: #970e3d !important;
        border-color: #c43464 !important;
        font-size: 14px !important;
    }


    @media only screen and (max-width: 650px) {
        .products-grid  {
            max-width: 500px !important;
            display: block;
            align-items: center;
            padding: 30px;
            margin-left: auto;
            margin-right: auto;
        }
    }


    @media only screen and (max-width: 650px) {
        .product {
            margin: 20px;
            display: block;
            align-items: center;
            padding: 20px;
        }
    }

    @media only screen and (max-width: 650px) {
        .btn-outline{
            display: flex;
             justify-content: center;

        }
    }


